    color: #ADB934;
    text-align: center;
    line-height: 13px;
    border-color: #ADB934; }
  .b-checkbox input:checked + label {
    color: #ADB934; }

.b-checkbox label {
  display: inline-block;
  position: relative;
  padding-left: 2rem;
  cursor: pointer; }
  .b-checkbox label:before {
    content: "";
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    width: 13px;
    height: 13px;
    background-color: #fff;
    border: 1px solid #ccc; }

.b-checkbox__link {
  font-weight: 500;
