  text-decoration: underline; }
  .b-checkbox__link:hover {
    opacity: .7; }

.b-spoiler {
  overflow: hidden; }
  .b-spoiler h3 {
    cursor: pointer; }
    .b-spoiler h3.is-active + p {
      max-height: 10rem;
      margin: 2.5rem 0 0 0; }
  .b-spoiler p {
    margin: 0;
    max-height: 0;
    transition: all .4s; }

.b-tooltip {
  position: absolute;
  display: none;
  left: -7.5rem;
  bottom: 0;
  transform: translate(0, 130%);
  padding: .7rem 2rem;
  background-color: #D6E3F1;
  color: #878787;
  transition: opacity .3s;
  opacity: 0; }
  .b-tooltip:after {
    content: "";
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50%, -100%);
    width: 0;
    height: 0;
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    border-bottom: 7px solid #D6E3F1; }

.b-switcher {
  position: relative;
  display: inline-block;
  margin-top: .2rem;
  font-size: 14px;
  color: #ADB934; }
  .b-switcher__heart {
    position: relative;
    display: inline-block;
    background-color: #ADB934;
    width: 3rem;
    height: 1.5rem;
    border-radius: 9000rem;
    cursor: pointer; }
    .b-switcher__heart:before {
      content: "";
      width: 1rem;
      height: 1rem;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 2px;
      background-color: #fff;
      border: 1px solid #fff;
      border-radius: 50%;
      transition: transform .4s; }
  .b-switcher__value {
    position: absolute;
    width: 3rem;
    top: 50%;
    right: -0.5rem;
    transform: translate(100%, -65%);
    opacity: 0;
    cursor: pointer; }
    .b-switcher__value--off {
      opacity: 1; }
  .b-switcher input {
    display: none; }
    .b-switcher input:checked + .switcher__heart:before {
      transform: translate(1.4rem, -50%); }
    .b-switcher input:checked ~ .switcher__value--on {
      opacity: 1; }
    .b-switcher input:checked ~ .switcher__value--off {
      opacity: 0; }
